.bundle-container {
  display: flex;
}

.bundle-sub {
  flex-direction: column;
}

.bundle-title {
  display: flex;
  align-items: flex-end;
  font-family: "CiscoSansTTLight", sans-serif;
  color: #1b1c1d;
}

.bundle-content {
  font-family: "CiscoSansTTLight", sans-serif;
  color: #1b1c1d;
}

.paragraph-one {
  margin-top: 0px;
  margin-bottom: 14px;
  line-height: 24px;
  font-size: 16px;
}

.paragraph-two {
  margin-bottom: 0px;
  line-height: 20px;
  font-size: 14px;
}

.bundle-btn {
  background-color: white;
  width: 138px;
  height: 56px;
  background: #0076d5;
  border: 1px solid #0076d5;
  box-sizing: border-box;
  border-radius: 48px;
  padding: 12px, 32px;
  color: #ffffff;
  font-family: "CiscoSansTT-Medium", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  transition: color background-color border-color 0.1s;
}

.bundle-btn:hover {
  color: white;
  background-color: #0051af;
  border-color: #0051af;
}

/* For mobile phones: */
@media (min-width: 320px) {
  .bundle-container {
    width: 320px;
    padding: 16px 0;
    flex-direction: column;
  }

  .bundle-sub {
    width: 320px;
    margin-top: 26px;
    padding: 0 16px;
  }

  .bundle-title {
    width: 288px;
    margin: 26px 0px 19px 0;
    line-height: 32px;
    font-size: 24px;
  }

  .bundle-content {
    width: 288px;
    margin-bottom: 25px;
  }
}

@media all and (min-width: 768px) {
  /* For tablets */
  .bundle-container {
    width: 768px;
    padding: 64px 0;
    flex-direction: column;
  }

  .bundle-sub {
    width: 608px;
    margin-top: 41px;
    padding: 0 80px;
  }

  .bundle-title {
    width: 608px;
    margin: 7px 0px 33px 0;
    line-height: 40px;
    font-size: 32px;
  }

  .bundle-content {
    width: 608px;
    margin-bottom: 42px;
  }
}

@media all and (min-width: 1440px) {
  /* For desktop */
  .bundle-container {
    width: 1440px;
    padding: 80px 0;
    flex-direction: row;
  }

  .bundle-sub {
    width: 580px;
    margin: 0px 249px 0px 145px;
    padding-right: 48px;
  }

  .bundle-title {
    width: 532px;
    margin: 43.5px 0px 33px 0;
    line-height: 40px;
    font-size: 32px;
  }

  .bundle-content {
    width: 532px;
    margin-bottom: 42px;
  }
}
