.service-header {
  text-align: center;
  font-family: "CiscoSansTTLight", sans-serif;
  color: #1b1c1d;
}

.content {
  display: flex;
  padding-bottom: 43px;
}

.content::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  border-top: 1px solid #e6e7e8;
  border-bottom: 1px solid #e6e7e8;
}

.eyebrow {
  height: 20px;
  margin-top: 44px;
  font-family: "CiscoSansTT-Medium", sans-serif;
  font-weight: 500;
  font-size: 14px;
  color: #1b1c1d;
}

.subtile {
  margin-top: 20px;
  padding-top: 6px;
  padding-bottom: 7px;
  font-family: "CiscoSansTTLight", sans-serif;
  font-size: 20px;
  line-height: 28px;
  color: #1b1c1d;
}

.paragraph {
  margin-top: 12px;
  padding-top: 5px;
  padding-bottom: 7px;
  /* height: 96px; */
  font-family: "CiscoSansTTLight", sans-serif;
  font-size: 16px;
  line-height: 24px;
  color: #606162;
}

.details-container {
  width: 102px;
  height: 24px;
  margin-top: 5px;
  margin-bottom: 0;
  position: relative;
  display: flex;
  align-self: flex-end;
  flex-direction: row;
  transition: transform 0.3s ease;
  transform: translateX(0px);
}

.details-container::after {
  align-self: flex-end;
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 72%;
  height: 1px;
  background-color: #0076d5;
  transform: scale(0, 1);
}

.details-container:hover::after {
  align-self: flex-end;
  transform: scale(1, 1);
}

.details {
  display: flex;
  width: 82px;
  padding-top: 2px;
  font-style: normal;
  font-family: "CiscoSansTT-Medium", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #0076d5;
  padding-top: 0.5px;
  padding-bottom: 2.5px;
  transition: margin-right padding-top 0.1s;
}

.details:hover {
  margin-right: 2px;
  padding-top: 0px;
}

.icon {
  display: flex;
  margin-top: 6px;
  margin-left: 0px;
  padding-top: 1px;
  width: 5.5px;
  height: 10.98px;
  transition: margin-left 0.1s;
  fill: #0076d5;
}

.icon:hover {
  margin-left: 2px;
}

.btn {
  background-color: white;
  margin-top: 48px;

  width: 145px;
  height: 56px;
  border: 1px solid #0076d5;
  box-sizing: border-box;
  border-radius: 48px;
  padding: 12px, 32px;
  color: #0076d5;
  font-family: "CiscoSansTT-Medium", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  transition: color background-color border-color 0.1s;
}

.btn:hover {
  color: white;
  background-color: #0051af;
  border-color: #0051af;
}

/* For mobile phones: */
@media (min-width: 320px) {
  .service-container {
    width: 320px;
  }

  .service-header {
    margin-top: 41px;
    margin-left: 16px;
    width: 288px;
    line-height: 32px;
    font-size: 24px;
  }

  .content {
    margin-top: 24px;
    margin-left: 16px;
    width: 288px;
    /* height: 313px; */
    position: relative;
    flex-direction: column;
  }

  .content::before {
    width: 136px;
    left: 76px;
  }

  .sub-container {
    width: 288px;
    /* height: 313px; */
  }

  .eyebrow {
    width: 288px;
  }

  .subtile {
    width: 288px;
  }

  .paragraph {
    width: 288px;
  }

  .divider {
    width: 0px;
  }

  .btn {
    margin-left: 87.5px;
  }
}

@media all and (min-width: 768px) {
  /* For tablets */
  .service-container {
    width: 768px;
  }

  .service-header {
    margin-top: 57px;
    margin-left: 80px;
    width: 608px;
    line-height: 40px;
    font-size: 32px;
  }

  .content {
    margin-top: 38px;
    margin-left: 80px;
    width: 608px;
    border-top: 1px solid #e6e7e8;
    border-bottom: 1px solid #e6e7e8;
    flex-direction: row;
  }

  .content::before {
    width: 0;
  }

  .sub-container {
    width: 292px;
  }

  .eyebrow {
    width: 268px;
  }

  .subtile {
    width: 268px;
  }

  .paragraph {
    width: 268px;
  }

  .divider {
    width: 24px;
  }

  .btn {
    margin-left: 311.5px;
  }
}

@media all and (min-width: 1440px) {
  /* For desktop */
  .service-container {
    width: 1440px;
  }

  .service-header {
    margin-top: 73px;
    margin-left: 370px;
    width: 700px;
    line-height: 40px;
    font-size: 32px;
  }

  .content {
    margin-top: 38px;
    margin-left: 370px;
    width: 700px;
    border-top: 1px solid #e6e7e8;
    border-bottom: 1px solid #e6e7e8;
    flex-direction: row;
  }

  .content::before {
    width: 0px;
  }

  .sub-container {
    width: 338px;
  }

  .eyebrow {
    width: 293px;
  }

  .subtile {
    width: 293px;
  }

  .paragraph {
    width: 293px;
  }

  .divider {
    width: 24px;
  }

  .btn {
    margin-left: 647.5px;
  }
}
