.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* For mobile phones: */
@media (min-width: 320px) {
  .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

@media all and (min-width: 768px) {
  /* For tablets */
  .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

@media all and (min-width: 1440px) {
  /* For desktop */
  .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
